                //     --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
                //  Override Variables
                --font-family-sans-serif: Oxygen, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
                // Page Variables
                --font-family-serif: Raleway, Arial;
                --font-family-other: Raleway, Spartan, Oxygen;
            }
            
            // html {
            //     scroll-behavior: smooth;
            // }
            .family-other {
                font-family: var(--font-family-other);
                font-weight: 700;
            }
            
            body {
                font-family: var(--font-family-sans-serif);
                position: relative;
            }
            
            .site-header {
                background-color: #212121;
                background-image: var(--topography-dark);
                background-position: center center;
                padding-bottom: 0;
            }
            
            .site-header .site-nav {
                background: linear-gradient(180deg, #202020 95%, rgba(0, 0, 0, 0));
                // background-color: #212121;
                // background-image: var(--topography-dark);
                z-index: 1;
            }
            
            .site-header .nav-brand {
                font-size: 90%;
                line-height: 1.2em;
            }
            
            .site-header .brand-name {
                font-weight: 700;
                font-size: 1.5em;
                margin: 0;
            }
            
            .site-header .brand-code {
                font-size: 1.3em;
                margin: 0;
            }
            
            .site-header .burger-icon {
                font-size: 1.5em;
            }
            
            .site-header .brand-sub {
                font-size: 0.5em;
                font-weight: 400;
                margin: 0;
            }
            
            .site-header .active {
                color: var(--danger);
            }
            
            .site-header .active:hover {
                color: var(--danger-hover);
            }
            
            .site-header .layout-landing {
                min-height: 100vh;
            }
            
            .site-header .page-section-title {
                font-size: 7.5em;
                font-family: var(--font-family-other);
            }
            
            .site-header .page-section-title-mobile {
                font-size: 2em;
                font-family: var(--font-family-other);
                background: -webkit-linear-gradient(var(--danger), var(--danger), var(--primary));
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            
            .site-header .page-section-text {
                font-size: 6.5em;
            }
            
            .page-section-skills {
                background-color: var(--danger);
                background-image: var(--topography-danger);
                padding-top: 1em;
            }
            
            .page-section-skills .container {
                background-color: rgba(50, 50, 50, 0.8);
                border-radius: 1em;
            }
            
            .page-section-projects {
                background-color: var(--primary);
                background-image: var(--topography-primary);
                padding-top: 1em;
            }
            
            .skill-html {
                background: var(--primary);
            }
            
            #skill-html {
                width: 100%;
            }
            
            .page-section-projects .container {
                background-color: rgba(50, 50, 50, 0.8);
                height: 100vh;
                border-radius: 1em;
            }
            
            .page-section-skills .skills-header {
                font-size: 2em;
                font-family: var(--font-family-serif);
                margin-top: 0;
            }
            
            .page-section-projects .skills-projects {
                font-size: 2em;
                font-family: var(--font-family-serif);
                margin-top: 0;
            }
            
            .site-footer {
                position: fixed;
                top: 50%;
                right: 0;
                -webkit-transform: translateY(-50%);
                -ms-transform: translateY(-50%);
                transform: translateY(-50%);
                width: 3em;
                background: linear-gradient(180deg, rgba(15, 15, 15, 0.7), rgba(10, 10, 10, 0.8));
                border-radius: 1em 0 0 1em;
            }
            
            .site-footer .a {
                display: block;
                text-align: center;
                padding: 10px;
                transition: all 0.3s ease;
            }
            
            .site-footer .layout-icon {
                font-size: 2em;
            }
            
            .site-footer-small {
                position: fixed;
                -webkit-transform: translateX(50%);
                -ms-transform: translateX(50%);
                transform: translateX(50%);
                padding-top: 0.3em;
                padding-bottom: 0.3em;
                bottom: 0;
                width: 50vw;
                background: linear-gradient(180deg, rgba(15, 15, 15, 0.7), rgba(10, 10, 10, .8));
                border-radius: 1em 1em 0 0;
            }
            
            .site-footer-small .a {
                display: inline-block;
                text-align: center;
                padding: 10px;
                transition: all 0.3s ease;
            }
            
            .site-footer-small .layout-icon-small {
                font-size: 1.5em;
            }
            
            .page-offset {
                padding-top: 70px;
            }
            
            .wel-slash {
                font-size: 8em;
                margin: 0;
                padding: 0;
                background: -webkit-linear-gradient(var(--danger), var(--danger), var(--primary));
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            
            .site-footer-actual {
                background: rgb(20, 20, 20);
                margin-bottom: 0;
                text-align: center;
                padding-bottom: 0.5rem;
                color: rgba(250, 250, 250, 0.4);
            }
            
            .site-footer-actual p {
                padding-top: 0.5em;
                font-family: var(--font-family-other);
                font-size: 0.75em;
            }